import React, { Component } from "react";
import { Modal, ModalHeader, ModalBody } from "reactstrap";
import { hideModal } from "../../../../actions/datamaster_action";
import Camera from "react-html5-camera-photo";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { setCamera } from "../../../../actions/databarang_action";

const maptostate = (state) => {
  return {
    setCamera: state.databarang.setCamera,
    hideModal: state.datamaster.modalDialog,
  };
};
class ModalWebCam extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleTakePhoto(url) {
    // console.log(url);
    this.props.dispatch(setCamera(url));
    // this.props.dispatch(hideModal());
  }

  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }

  render() {
    return (
      <Modal
        backdrop="static"
        keyboard={false}
        isOpen={this.props.isOpen}
        toggle={() => this.props.dispatch(hideModal())}
      >
        <ModalHeader> {this.props.title} </ModalHeader>
        <form onSubmit={this.props.handleSubmit} autoComplete="off">
          <ModalBody>
            <div className="row">
              <div className="container">{this.loadcamera()}</div>
            </div>
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              <i className="fa fa-arrow-left"></i> Kembali
            </button>
          </ModalBody>
        </form>
      </Modal>
    );
  }
}

ModalWebCam = reduxForm({
  form: "ModalWebCam",
  enableReinitialize: true,
})(ModalWebCam);
export default connect(maptostate, null)(ModalWebCam);
