import {
  React,
  imgfound,
  connect,
  ReanderField,
  createNumberMask,
  Field,
  reduxForm,
  Camera,
  Component,
  PanelHeader,
  Panel,
  ReanderSelect,
  Gramasi,
  showModal,
  hideModal,
  reset,
  localStorageencryp,
  getData,
  PanelBody,
  getDataNoTimeOut2,
  getData2,
  SkeletonLoading,
} from "../../../../components/helpers/library";
import { io } from "socket.io-client";
// import { showModalWebcam } from "../../../store/actions/Barang/dataBarang_action";

// import { setCamera, tampilModalWebCam } from "./ModuleDataBarang";
import {
  NotifSucces,
  ToastNotification,
} from "../../../../components/helpers/notification";
import {
  getDataBaki,
  GetDataGroup,
  getDataGudang,
  getDataJenis,
  getMasterAtribut,
  // getMasterLabeling,
  getMasterPabrik,
  showModalWebcam,
} from "../../../../actions/datamaster_action";
import { setCamera } from "../../TambahBarang/Module";
import {
  convertBase64,
  getItem,
  getUserdata,
  setItem,
} from "../../../../components/helpers/function";
import ModalWebCam from "./ModalWebCam";
import { Kadar } from "../../../../Validasi/normalize";

const maptostate = (state) => {
  let dataatr = getItem("dataattr");
  let namaatr = "-";
  let mdl = "";
  if (dataatr.model) {
    mdl = "MDL";
  }
  if (dataatr.length !== 0) {
    namaatr =
      mdl +
      " " +
      dataatr.level1 +
      " " +
      dataatr.level2 +
      " " +
      dataatr.level3 +
      " " +
      dataatr.level4 +
      " " +
      dataatr.pabrik;
  }
  // console.log(dataatr);
  // console.log(namaatr);
  return {
    modalwebcam: state.datamaster.showModalWebcam,
    foto: state.databarang.showEditBarang.foto,
    isLoading: state.datamaster.isLoading,
    setCamera: state.databarang.setCamera,
    hideModal: state.datamaster.modalDialog,
    // DataLabeling: state.datamaster.getMasterLabeling,
    DataGroup: state.datamaster.GetDataGroup,
    DataJenis: state.datamaster.getDataJenis,
    DataGudang: state.datamaster.getDataGudang,
    DataBaki: state.datamaster.getDataBaki,
    initialValues: {
      atribut: namaatr,
      is_markis: "FALSE",
    },
  };
};
const server = process.env.REACT_APP_BACKEND_URL;

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});
class FormTambahDataBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
      ModalWebCam: false,
      isLoadingBaki: false,
      isLoadingJenis: false,
      showModalForm: false,
      loadingData: false,
      cekStatus: false,
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
      databaki: [],
      DataLabeling: [],
      DataGroup: [],
      DataJenis: [],
      DataGudang: [],
      loadimage: imgfound,
      name_type: "SP",
    };
  }
  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }
  handleTakePhoto(url) {
    this.props.dispatch(setCamera(url));
    this.props.change("foto", url);
    document.getElementById("ongkos").focus();
  }
  shofFormModal(id) {
    this.setState({
      showModalForm: id,
    });
    this.props.dispatch(showModal());
  }

  getDataDetailBarangBos(params) {
    clearTimeout(this.delayTimer);
    this.delayTimer = setTimeout(() => {
      this.setState({ loadingData: true });

      if (this.state.stock_input === undefined) {
        this.setState({ loadingData: false });
        ToastNotification("info", "Stock Input Belum Dipilih");
        return false;
      }
      this.setState({
        no_kirim: params.trim(),
      });
      getDataNoTimeOut2(
        "kirim-labeling/jenises?no_kirim=" +
          params.trim() +
          "&kode_toko=" +
          this.state.stock_input
      )
        .then((res) => {
          if (res.data.length === 0) {
            this.setState({
              loadingData: false,
            });
            ToastNotification("info", "No Kirim Barang Sudah Tidak Ada");
          } else {
            let hasil = [];
            res.data.forEach((element) => {
              let row = {
                value: element.kode_dept,
                name: element.kode_dept,
              };
              hasil.push(row);
            });
            this.setState({
              DataJenis: hasil,
              loadingData: false,
            });
          }
        })
        .catch((err) => {
          ToastNotification(
            "info",
            err.response?.data.message || "Gagal Mengirim Data Keserver"
          );
          this.setState({
            loadingData: false,
          });
        });
    }, 1500);
  }

  FormBatal(id) {
    if (id === true) {
      this.props.dispatch(hideModal());
      this.props.dispatch(reset("TambahBarangBanyak"));
    } else {
      this.props.dispatch(hideModal());
      this.props.dispatch(reset("FormDataAtribut"));
    }
  }
  resetKamera() {
    this.props.change("foto", "");
    this.props.dispatch(setCamera(false));
    // this.props.dispatch(showModalWebcam(false));
  }
  componentDidMount() {
    const socket = io.connect(server.replace("/api/v1/", ""));
    socket.on("timbangan", (data) => {
      if (getUserdata().user_id === data.user_id) {
        this.props.change("berat_asli", data.value);
      }
    });

    getData("baki/get/all").then((res) => {
      // console.log(res.data);
      this.setState({
        DataLabeling: res.data,
      });
    });

    getData("gudang/get/all").then((res) => {
      this.setState({
        DataGudang: res.data,
      });
    });

    this.props.change("jumlah_barang", 1);
    setItem("dataattr", []);
    localStorageencryp("kadar_type", this.state.name_type);
    localStorageencryp("pabrik", "-");
    this.props.dispatch(getMasterPabrik());
    this.props.dispatch(getMasterAtribut());
    // this.props.dispatch(getMasterLabeling());
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getDataJenis());
    this.props.dispatch(getDataGudang());
    this.props.dispatch(getDataBaki());
  }

  simpanDataAtrr(val) {
    let hasil = {
      level1: val.level1 || "",
      level2: val.level2 || "",
      level3: val.level3 || "",
      level4: val.level4 || "",
      custome: val.custome || "",
      kadar: val.kadar || "",
      pabrik: val.pabrik || "",
      model: val.model || false,
    };
    setItem("dataattr", hasil);
    NotifSucces("Berhasil Menambahkan Data Attribut !").then(() =>
      this.props.dispatch(hideModal())
    );
  }

  onValChange = (event) => {
    this.setState({
      name_type: event.target.value,
    });
    localStorageencryp("kadar_type", event.target.value);
  };

  tampilModalWebCam() {
    this.props.dispatch(setCamera(false));
    // this.props.dispatch(showModal());
    this.props.dispatch(showModalWebcam(true));

    this.setState({ ModalWebCam: true });
  }
  checkQty(e) {
    // console.log(e);
    if (e !== "") {
      let qty = document.getElementById("qty").value;
      if (qty === "") {
        this.setState({
          cekStatus: true,
        });
        ToastNotification("info", "Qty Tidak Boleh Kosong");
        this.props.change("jumlah_barang", 0);
        return false;
      } else {
        this.setState({
          cekStatus: false,
        });
      }
      if (parseInt(e) > parseInt(qty)) {
        this.setState({
          cekStatus: true,
        });
        ToastNotification("info", "Jumlah Tidak Boleh Lebih Dari Qty");
        this.props.change("jumlah_barang", 0);
        return false;
      } else {
        this.setState({
          cekStatus: false,
        });
      }
    }
  }

  getValue = async (event) => {
    const file = event.target.files[0];
    const base64 = await convertBase64(file);
    this.props.dispatch(setCamera(base64));
  };
  carikategori(e) {
    getData2(
      "kirim-labeling?kode_toko=" +
        this.state.stock_input +
        "&no_kirim=" +
        this.state.no_kirim +
        "&kode_dept=" +
        e
    )
      .then((res) => {
        document.getElementById("sales_pabrik_bos").focus();
        this.props.change("kode_group", res.data.kode_group);
        this.props.change("qty_bos", res.data.stock_akhir);
        this.props.change("berat_bos", res.data.berat_akhir.toFixed(3));
        this.props.change(
          "sales_pabrik_bos",
          res.data.sales_parbik === undefined ? "-" : res.data.sales_parbik
        );
      })
      .catch((err) => {
        console.log(err);
        ToastNotification(
          "info",
          err.response?.data.message || "Terjadi Kesalahan Saat Request Data"
        );
      });
  }
  showModalWebcam() {
    this.props.dispatch(showModal());
  }
  render() {
    return (
      <Panel>
        <PanelHeader>Tambah Data Barang</PanelHeader>
        <PanelBody>
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <Field
                  name="kode_gudang"
                  component={ReanderSelect}
                  onChange={(e) => {
                    this.setState({
                      gudangSelected: e,
                    });
                  }}
                  options={this.state.DataGudang.filter(
                    (list) =>
                      list.kode_gudang !== undefined &&
                      list.kode_gudang !== "BELI" &&
                      list.kode_gudang !== "CUCI" &&
                      list.kode_gudang !== "TITIP" &&
                      list.kode_gudang !== "REPARASI"
                  ).map((list) => {
                    let data = {
                      value: list.kode_gudang,
                      name: list.kode_gudang + " - " + list.nama_gudang,
                    };
                    return data;
                  })}
                  label="Kode Gudang"
                  placeholder="Pilih Kode Gudang"
                />
              </div>
              <div className="col-lg-4">
                {this.state.isLoadingBaki === false ? (
                  <Field
                    label="Kode Baki"
                    name="kode_baki"
                    options={this.props.DataBaki.filter(
                      (fil) =>
                        fil.kode_baki !== "TITIPJUAL" &&
                        fil.kode_gudang === this.state.gudangSelected
                    ).map((databaki) => {
                      let data = {
                        value: databaki.kode_baki,
                        name: databaki.kode_baki + " - " + databaki.nama_baki,
                      };
                      return data || [];
                    })}
                    placeholder={"Silahkan Pilih Kode Baki"}
                    component={ReanderSelect}
                  />
                ) : (
                  <SkeletonLoading label="Kode Baki" />
                )}
              </div>
              <div className="col-lg-4">
                <Field
                  name="kode_group"
                  component={ReanderSelect}
                  onChange={(e) => {
                    this.setState({
                      groupSelected: e,
                    });
                  }}
                  options={this.props.DataGroup.filter(
                    (list) => list.kode_group !== undefined
                  ).map((list) => {
                    let data = {
                      value: list.kode_group,
                      name: list.kode_group + " - " + list.nama_group,
                    };
                    return data;
                  })}
                  label="Kode Group"
                  placeholder="Pilih Kode Group"
                />
              </div>
              <div className="col-lg-4">
                {this.state.isLoadingJenis === false ? (
                  <Field
                    label="Kode Jenis"
                    name="kode_dept"
                    options={this.props.DataJenis.filter(
                      (fil) => fil.kode_group === this.state.groupSelected
                    ).map((DataJenis) => {
                      let data = {
                        value: DataJenis.kode_dept,
                        name: DataJenis.kode_dept + " - " + DataJenis.nama_dept,
                      };
                      return data;
                    })}
                    placeholder={"Silahkan Pilih Kode Jenis"}
                    component={ReanderSelect}
                  />
                ) : (
                  <SkeletonLoading label="Kode Jenis" />
                )}
              </div>
            </div>
            <div className="row">
              <div className="col-lg-8">
                <div className="row">
                  <div className="col-lg-8">
                    <Field
                      name="nama_barang"
                      component={ReanderField}
                      label="Nama Barang"
                      placeholder="Masukan Nama Barang"
                    />
                  </div>
                  <div className="col-lg-4">
                    <Field
                      name="kadar_modal"
                      component={ReanderField}
                      label="Kadar Modal"
                      placeholder="Masukan Kadar Modal"
                      normalize={Kadar}
                    />
                  </div>
                </div>
                <div className="row">
                  <div className="col-lg-8">
                    <Field
                      name="nama_atribut"
                      component={ReanderField}
                      label="Nama Atribut"
                      placeholder="Masukan Nama Atribut"
                    />
                  </div>
                  <div className="col-lg-4">
                    <Field
                      name="harga_atribut"
                      component={ReanderField}
                      label="Harga Atribut"
                      placeholder="Masukan Harga Atribut"
                      {...currencyMask}
                    />
                  </div>
                </div>
                <div className="row">
                  <div className="col-lg-8">
                    <Field
                      name="berat_asli"
                      component={ReanderField}
                      label="Berat Asli"
                      placeholder="Masukan Berat Asli"
                      normalize={Gramasi}
                    />
                  </div>
                  {/* <div className="col-lg-4">
                    <Field
                      name="harga_jual"
                      component={ReanderField}
                      label="Harga Jual"
                      placeholder="Masukan Harga Jual"
                      {...currencyMask}
                    />
                  </div> */}
                </div>
                <div className="row">
                  <div className="col-lg-6 d-none">
                    <Field
                      name="ongkos"
                      component={ReanderField}
                      label="Ongkos"
                      placeholder="Masukan Ongkos"
                      {...currencyMask}
                    />
                  </div>
                  <div className="col-lg-6">
                    <Field
                      name="is_markis"
                      component={ReanderSelect}
                      options={[
                        { value: "TRUE", name: "MARKIS" },
                        {
                          value: "FALSE",
                          name: "NORMAL",
                        },
                      ]}
                      label="Markis"
                      placeholder="pilih Markis"
                    />
                  </div>
                  <div className="col-lg-6">
                    <Field
                      name="kode_intern"
                      component={ReanderField}
                      label="Kode Intern"
                      placeholder="Masukan Kode Intern"
                    />
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <div className="row">
                  <div className="col-lg-4">
                    <img
                      src={
                        this.props.setCamera === false
                          ? "http://54.151.162.118/nagagold_dev/assets/NsiPic/temp.jpg"
                          : this.props.setCamera
                      }
                      alt="img"
                      height="210px"
                      width="305px"
                    />
                  </div>
                </div>
                <div className="row">
                  <div className="col-lg-12 mb-8">
                    <br />
                    <label> &nbsp; </label>
                    <div className="row">
                      <div className="col-8">
                        <div className="input-group">
                          <div className="custom-file">
                            <input
                              type="file"
                              name="photo"
                              autoComplete="off"
                              className="custom-file-input"
                              id="exampleInputFile"
                              onChange={(e) => this.getValue(e)}
                            />
                            <label
                              className="custom-file-label"
                              htmlFor="exampleInputFile"
                            >
                              Pilih gambar
                            </label>
                          </div>
                        </div>
                      </div>
                      <div className="col-4">
                        <button
                          type="button"
                          className="btn btn-block btn-primary"
                          onClick={() => this.showModalWebcam()}
                        >
                          {" "}
                          WebCam{" "}
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row d-none">
              <div className="col-lg-6">
                <Field
                  readOnly="readonly"
                  name="kode_barang"
                  component={ReanderField}
                  label="Kode barang"
                  placeholder="Masukan Kode barang"
                />
              </div>
              <div className="col-lg-6">
                <Field
                  readOnly="readonly"
                  name="total_harga"
                  component={ReanderField}
                  label="Total Harga"
                  placeholder="Masukan Total Harga"
                />
              </div>
            </div>
            <div className="row justify-content-end">
              <div className="col-lg-3 text-right">
                <button
                  type="submit"
                  className="btn btn-primary mt-1 btn-block"
                >
                  Simpan
                </button>
              </div>
              <div className="col-lg-3">
                <button
                  type="button"
                  onClick={() => this.props.kembali()}
                  className="btn btn-warning mt-1 btn-block"
                >
                  Kembali
                </button>
              </div>
            </div>
            <ModalWebCam
              isLoading={this.state.isLoading}
              title={"Ambil Foto"}
              isOpen={this.props.hideModal}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </form>
        </PanelBody>
      </Panel>
    );
  }
}

FormTambahDataBarang = reduxForm({
  form: "FormTambahDataBarang",
  enableReinitialize: true,
})(FormTambahDataBarang);
export default connect(maptostate)(FormTambahDataBarang);
